import config from '../../../../configs/themeConfig'
import Sidebar from '@components/sidebar'
import { useDispatch } from 'react-redux'
// ** Third Party Components
import { Button, FormGroup, Label  } from 'reactstrap'
import { AvForm, AvInput } from 'availity-reactstrap-validation-safe'
import { postCompany, upload, editCompany } from '../store/action'
import { useState } from 'react'
const SidebarNewUsers = ({ open, toggleSidebar, companyInfo }) => {
  const dispatch = useDispatch()
  const [clogo, setLogo] = useState()
  const onSubmit = (e, errors) => {
    if (!e.length) {
      const {company, city, branch } = e.target.elements
      if (companyInfo?.id) { 
        dispatch(editCompany(companyInfo?.id, {company: company.value, city: city.value, branch: branch.value}))
      } else {
        dispatch(postCompany({company: company.value, city: city.value, branch: branch.value, logo: localStorage.getItem('clogo') }))
      }
      toggleSidebar()
    }
    e.preventDefault()
  }

  const changeHandler = (event) => {
    event.preventDefault()
    const formData = new FormData()
    if (companyInfo?.id) {
      formData.append('id', companyInfo?.id) 
    }
    formData.append('logo', event.target.files[0])
    dispatch(upload(formData))
  }
  return (
    <Sidebar
      size='sm'
      open={open}
      title='Company details'
      headerClassName='mb-1'
      contentClassName='pt-0'
      toggleSidebar={toggleSidebar}
    >
      <AvForm onSubmit={onSubmit}>
        <FormGroup>
          <Label for='company_name'>Company Name</Label>
          <AvInput name='company' id='company_name' placeholder='Company Name'  required value={companyInfo?.company} />
        </FormGroup>
        <FormGroup>
          <Label for='city'>City</Label>
          <AvInput name='city' id='city' placeholder='city'  required  value={companyInfo?.city}     />
        </FormGroup>
        <FormGroup>
          <Label for='branch'>Branch</Label>
          <AvInput name='branch' id='branch' placeholder='branch'  required   value={companyInfo?.branch}  />
        </FormGroup>
        <FormGroup>
          <Label for='branch'>Logo</Label>
          <AvInput name='logo' type="file"  onChange={event => changeHandler(event)} accept="image/png, image/gif, image/jpeg" />
          {companyInfo?.logo ? <img src={`${config.app.serverApi}/images/${companyInfo?.logo}`} width={100}/> : ''}
        </FormGroup>
        <Button type='submit' className='mr-1' color='primary'>
          Submit
        </Button>
        <Button type='reset' color='secondary' outline onClick={toggleSidebar}>
          Cancel
        </Button>
      </AvForm>
    </Sidebar>
  )
}

export default SidebarNewUsers
